<template>
    <div class="fs layer0" ref="mc"></div>
    <div v-if="isUserActionRequired" id="startScreen"><button @click="Entry()">START AR</button></div>
</template>

<script>
    import * as THREE from 'three';
    import { GeolocationCoordinates } from "@/AR-Renderer/structs/geolocation_coordinates";
    import * as AR_Renderer from "@/AR-Renderer/ar_renderer";

    export default {
        name: 'GeolocationScene',
        props: {
            sceneData: Object
        },
        data() {
            return {
                isUserActionRequired: false,
            };
        },
        mounted() {
            this.Entry();
        },
        methods: {
            async Entry() {
                this.$parent.ShowLoadingIndicator(true);
                await this.$parent.SetCurrentScene(this);
                this.Launch();
            },

            async Launch() {
                await this.Setup();
                await this.Start();
                return;
            },

            async Setup() {
                this.scene = new THREE.Scene();
                this.camera = new THREE.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.01, 20);

                this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
                this.renderer.setPixelRatio(window.devicePixelRatio);
                this.renderer.setSize(window.innerWidth, window.innerHeight);
                this.renderer.xr.enabled = true;

                this.$refs.mc.appendChild(this.renderer.domElement);

                this.controller = this.renderer.xr.getController(0);
                this.scene.add(this.controller);

                this.Start = async () => {
                    navigator.xr.requestSession('immersive-ar', {
                        optionalFeatures: [
                            "dom-overlay"
                        ],
                        domOverlay: {
                            root: this.sceneData.unity
                        }
                    }).then(async (session) => {
                        this.isUserActionRequired = false;
                        this.session = session;
                        this.renderer.xr.setReferenceSpaceType('local');
                        await this.renderer.xr.setSession(session);

                        this.$parent.ShowLoadingIndicator(false);
                        AR_Renderer.onSceneLoaded(this.sceneData.sceneIndex);

                        this.animate();
                        return;
                    }).catch(()=>{
                        this.isUserActionRequired = true;
                    });
                }

                this.Stop = async () => {
                    if (this.session) {
                        await this.session.end();
                        return;
                    }
                    return;
                }
            },
            animate() {
                let geoCoords = {};

                this.AddOrientationEvents();

                const options = {
                    enableHighAccuracy: true
                }
                navigator.geolocation.watchPosition((geoPos) => { geoCoords = geoPos.coords; }, undefined, options);

                this.renderer.setAnimationLoop(() => {
                    this.renderer.render(this.scene, this.camera);
                    this.SendCameraTransformTounity(geoCoords);
                });
                this.updateNorth = true;
            },

            AddOrientationEvents(){
                if (window.DeviceOrientationEvent) {
                    switch (this.CheckDevice()) {
                        case "iOS":
                            addEventListener('deviceorientation', this.OrientationHandler, true);
                            break;
                        case "Android":
                            addEventListener('deviceorientationabsolute', this.OrientationHandler, true);
                            break;
                        default:
                            alert("Device orientation is not supported");
                            return;
                    }
                } else{
                    alert ("Device orientation is not supported");
                }
            },

            CheckDevice() {
                var userAgent = navigator.userAgent || navigator.vendor || window.opera;
                if (/android/i.test(userAgent)) {
                    return "Android";
                }
                if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
                    return "iOS";
                }
                return "unknown";
            },

            OrientationHandler(e) {
                if (this.updateNorth) {
                    if (e.webkitCompassHeading) {
                        this.northHeading = this.compassHeading(e.webkitCompassHeading, e.beta, e.gamma);
                    } else {
                        this.northHeading = this.compassHeading(e.alpha, e.beta, e.gamma);
                    }
                }
            },

            SendCameraTransformTounity(geoCoords) {
                let matrix = this.camera.matrixWorld.elements;
                let fov = this.camera.fov;
                let geoPos = new GeolocationCoordinates();

                if (geoCoords) {
                    geoPos = new GeolocationCoordinates(
                        geoCoords.accuracy,
                        geoCoords.altitude,
                        this.northHeading,
                        geoCoords.latitude,
                        geoCoords.longitude,
                        geoCoords.speed
                    )
                }

                AR_Renderer.onGeolocationTracking(matrix, fov, geoPos);
            },

            compassHeading (alpha, beta, gamma) {
                const alphaRad = alpha * (Math.PI / 180);
                const betaRad = beta * (Math.PI / 180);
                const gammaRad = gamma * (Math.PI / 180);

                const cA = Math.cos(alphaRad);
                const sA = Math.sin(alphaRad);
                const cB = Math.cos(betaRad);
                const sB = Math.sin(betaRad);
                const cG = Math.cos(gammaRad);
                const sG = Math.sin(gammaRad);

                const rA = - cA * sG - sA * sB * cG;
                const rB = - sA * sG + cA * sB * cG;
                const rC = - cB * cG;

                let compassHeading = Math.atan(rA / rB);

                if (rB < 0) {
                    compassHeading += Math.PI;
                } else if (rA < 0) {
                    compassHeading += 2 * Math.PI;
                }

                compassHeading *= 180 / Math.PI;

                return compassHeading;
            }
        }
    }
</script>

<style scoped>
    @import '../css/styles.css';

    .debug {
        width: 100%;
        height: 100%;
    }

    #startScreen {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        backdrop-filter: blur(10px);
        justify-content:flex-end;
        align-items: center;
        flex-direction: column;
        padding: 50px;
        display: flex;
        z-index: 1;
    }

        #startScreen button {
            padding: 10px;
            background-color: transparent;
            border-radius: 10px;
            font-size: 14px;
            color: darkgrey;
            font-weight: bold;
            border: 1px solid darkgrey;
        }

            #startScreen button:active {
                color: lightgrey;
                border: 2px solid lightgrey;
            }
</style>
